<template>
  <v-btn
    color="primary"
    :loading="loading"
    text
    @click="build"
  >
    Build Site
  </v-btn>
</template>

<script>
  export default {
    data () {
      return {
        loading: false,
      }
    },
    computed: {
      site: function () {
        return this.$store.state.site
      }
    },
    methods: {
      build () {
        this.loading = true
        this.api.get('sites/action/' + this.site)
          .then(result => {
            console.log(result.data)
            this.loading = false
          }, error => {
            this.error = error
            this.loading = false
          })
      }
    }
  }
</script>
